}

/*---------------------------------------------------------------------
                             Tasks Card
-----------------------------------------------------------------------*/
.tasks-card {
  padding: 0 0 15px 0;
  border-bottom: 1px solid #eef0f4;
  margin: 0 0 15px 0;
}

.tasks-card .close {
  font-size: 12px;
}

.tasks-card:last-child {
  border: none;
